.navbar {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 1rem 2rem;
    background-color: black;
  }
  
  .navbar-logo {
    height: 60px; /* Adjust as needed */
  }
  
  .navbar-center {
    flex: 1;
    display: flex;
    justify-content: center;
  }
  
  .navbar-search {
    width: 60%;
    padding: 0.5rem;
    border-radius: 5px;
    border: none;
  }
  
  .navbar-links {
    list-style: none;
    display: flex;
    margin: 0;
    padding: 0;
  }
  
  .navbar-links li {
    margin: 0 1rem;
  }
  
  .navbar-links a {
    color: white;
    text-decoration: none;
    font-size: 1rem;
    transition: color 0.3s;
  }
  
  .navbar-links a:hover {
    color: #ddd;
  }
  
  .navbar-right {
    display: flex;
    align-items: center;
  }
  
  .navbar-basket {
    color: white;
    font-size: 1.5rem;
    margin-left: 1rem;
    text-decoration: none;
    transition: color 0.3s;
  }
  
  .navbar-basket:hover {
    color: #ddd;
  }
  